@import url(https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap);
* {
  text-align: center;
  margin: 0;
} 

body {
  height: 100vh;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#f3206d), to(#fed636));
  background-image: linear-gradient(to right bottom, #f3206d, #fed636);
  background-size: cover;
  background-repeat: no-repeat;
  font-family: 'Sulphur Point', sans-serif;
}

input {
  font-family: 'Sulphur Point', sans-serif;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 20px;
  text-align: left;
  margin: 0 auto;
  padding: 5px;
}

.wrapper {
  padding: 2%;
  width: 30vw;
  background-color: #fafafa;
  margin: 0 auto;
  border-radius: 10px;
}

button {
  font-family: 'Sulphur Point', sans-serif;
  padding: 5px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  color: #fafafa;
}

.header {
  display: flex;
  justify-content: space-between;
  height: 5vh;
  background-color: rgb(255, 255, 255, 0.5);
  padding-left: 5%;
}


form {
    padding: 10%;
    padding-top: 15%;
}

#login-wrapper {
    margin-top: 10vh;
    height: 60vh;
}

#email, #password {
    margin-top: 10%;
    width: 80%;
    display: block;
}

#login,#signup {
    margin: 2.5%;
    margin-top: 10%;
    width: 30%;
    font-size: large;
}

#login {
    background-color: cornflowerblue;
}

#signup {
    background-color: darkseagreen;
}

#pomodoro-wrapper {
    margin-top: 2.5%;
    height: 70vh;
}

#signout {
    margin-right:30%;
    background-color: darkseagreen;
    opacity: 1;
}

#todo-wrapper {
    padding-left: 10%;
    padding-top: 5%;
    height: 80%;
    overflow-y: auto;
}

.todo {
    text-align: left;
    width: 85%;
    font-size: 20px;
    margin: 5px;
    border-bottom: gray 1px solid;
    list-style: none;
    word-break : break-all;
}

.delete {
    opacity: 0;
    float: right;
    color: red;
    cursor: pointer;
}

.delete:hover {
    opacity: 1;
}

.link {
    cursor: pointer;
}
#pomodoro-input {
    width: 60%;
}

#add-task {
    font-size: large;
    margin-left: 2.5%;
    background-color: coral;
}
#start {
    margin-top: 5%;
    font-size: large;
}

#content {
    margin-top: 5%;
    font-size: large;
}

#send-zone {
    margin-top: 5%;
}

#send-zone button {
    width: 20%;
    font-size: large;
    margin: 2.5%;
}

#done-btn {
    background-color: tomato;
}

#not-yet-btn {
    background-color: cadetblue;
}

#select-zone {
    margin: 2.5%;
}

#timer-zone {
    margin: 2.5%;
}

